  }
  .tabrow span {
    color: #666;
    text-decoration: none;
  }
  .tabrow li.selected {
    background: #fff;
    color: #111 !important;
    font-weight: bold;
    z-index: 2;
    border-bottom-color: #fff;
  }
  .tabrow li.selected span {
    background: #fff;
    color: #111 !important;
    font-weight: bold;
    z-index: 2;
    border-bottom-color: #fff;
  }
  .tabrow:before {
    position: absolute;
    content: ' ';
    width: 100%;
    bottom: 0;
    left: 0;
    border-bottom: 1px solid #bbb;
    z-index: 1;
  }
  .tabrow li:before,
  .tabrow li:after {
    border: 1px solid #bbb;
    position: absolute;
    bottom: -1px;
    width: 5px;
    height: 5px;
    content: ' ';
  }
  .tabrow li:before {
    left: -6px;
    border-bottom-right-radius: 6px;
    border-width: 0 1px 1px 0;
    /*    box-shadow: 2px 2px 0 #D1D1D1; */
  }
  .tabrow li:after {
    right: -6px;
    border-bottom-left-radius: 6px;
    border-width: 0 0 1px 1px;
    /*    box-shadow: -2px 2px 0 #D1D1D1; */
  }
  .tabrow li.selected:before {
    box-shadow: 2px 2px 0 #fff;
  }
  .tabrow li.selected:after {
    box-shadow: -2px 2px 0 #fff;
  }

  @media screen and (orientation: portrait) {
    .alertBox2 {
      top: 0 !important;
      left: 0 !important;
      width: 99% !important;
    }
  }
}
